import react,{useState} from 'react';
import NotificationsIcon from '@mui/icons-material/Notifications';
import Badge from '@mui/material/Badge';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';
import MyMenu from '../../components/MyMenu/MyMenu';



const notifications = [
    {
        id: 0,
        label: 'First notification'
    },
    {
        id: 1,
        label: 'Second notification'
    },
];




export default function Notification({iconColor}){

const [open,setOpen]=useState(false);
const [ancherEl,setAncherEl]=useState(null);


const handleOpen=(e)=>{
setAncherEl(e.currentTarget);
setOpen(true);

}


const handleClose=()=>{
setOpen(false);

}


const newNotification=`You have ${notifications.length} new notifications.`;
const noNotification='You have no new notification.';
return (
<div>
<Tooltip title={notifications.length===0?noNotification:newNotification}>
  <IconButton
  color={iconColor}
  onClick={notifications.length ? handleOpen : null}
  
  >
  <Badge badgeContent={notifications.length} color="error">
  <NotificationsIcon/>
</Badge>
  </IconButton>
  
</Tooltip>

  <MyMenu
  
   handleClose={handleClose}
   menuItems={notifications}
   anchorEl={ancherEl}
   open={open}
   
   />
  </div>
);
}
